<template>
  <div id="index">
    <header>
        <img src="../assets/img/email.png" alt="" @click="messageWindow = true">
        <div id="title">
            今日头条
            <img src="../assets/img/refresh.png" alt="">
        </div>
        <img src="../assets/img/search.png" alt="" @click="goSearch">
    </header>
    <div id="nav-bar">
        <div id="left">
            <ul>
                <li :class="{active : focusList[item.id] }" v-for="item in navList" :key="item.id" @click="goto(item.name,item.id)">
                    {{item.title}}
                </li>
            </ul>
        </div>
        <div id="right" @click="goToNavControl">
            +
        </div>
    </div>
    <router-view id="router-view" :data = data></router-view>
    <div id="message-window" v-if="messageWindow">
        <div id="message-container">
            <img src="../assets/img/message.png" alt="">
            <p>已加载好您感兴趣的头条</p>
            <button><a href="">立即打开</a></button>
            <div id="close-btn" @click="messageWindow = false">
                x
            </div>
        </div>
          
    </div>
  </div>
</template>

<script>
export default {
    name: 'Index',
    data(){
        return{
            navList: [],
            focusList: [true],
            data: [],
            messageWindow: false

        }
    },
    created(){
        let id = 0
        this.navList.forEach(element => {
            if(element.name === this.$router.currentRoute.name){
                this.focusList = []
                this.focusList[element.id] = true
                id = element.id
            }
        })
        this.getData(id)
        this.navList = this.$store.state.navList
    },
    methods: {
        goSearch(){
            this.$router.push({ name: 'Search' })
        },
        goto(name, id){
            // this.$router.push({ name })
            this.focusList = []
            this.focusList[id] = true
            this.getData(id)
        },
        getData(type){
            type = type === undefined ? 1 : type > 2 ? 1 : type
            this.axios.get(`https://www.shuipingguo.com/news/?type=${type}`).then((response) => {
                // console.log(response)
                this.data = response.data.data
            })
        },
        goToNavControl(){
            this.$router.push({ name: 'NavManage' })
        }
    }

}
</script>

<style lang='less' scoped>
#index{
    height: 100%;
    width: 100%;
    header{
        z-index: 2;
        position: fixed;
        height: 2.8rem;
        width: 100%;
        background: #d43d3d;
        display: flex;
        padding: 0 10px;
        box-sizing: border-box;
        align-items: center;
        justify-content: space-between;
        color: #fff;
        img{
            height: 1.2rem;
        }
        #title{
            font-size: 1.6rem;
            font-weight: 600;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 1.2rem;
            img{
                margin-left: 5px;
            }
        }
        .icon{
            font-size: 1.2rem;
            font-weight: 500;
        }
    }
    #nav-bar{
        z-index: 3;
        position: fixed;
        top: 2.8rem;
        height: 2.4rem;
        width: 100%;
        display: flex;
        align-items: center;
        background: rgb(236, 236, 236);
        overflow: hidden;
        #left{
            width: 86%;
            overflow-x: auto;
            -webkit-overflow-scrolling : touch;
            &::-webkit-scrollbar{
                display: none;
            }
            ul{
                list-style: none;
                display: flex;
                align-items: center;
                white-space: nowrap;
                padding: 0;
                margin: 0;
                height: 2rem;
                li{
                    font-size: 1em;
                    margin: 0 16px;
                    &:last-child{
                        margin-right: 50px;
                    }
                }
                .active{
                    color: #f85959;
                }
            }
        }
        #right{
            flex: 1;
            position: relative;
            font-size: 1.8em;
            color: #f85959;
            text-align: center;
            &::before{
                content: '';
                position: absolute;
                top: -.5rem;
                left: -1rem;
                height: 3.1rem;
                width: 1rem;
                background: linear-gradient(to right, rgba(255, 255, 255, 0),rgba(0, 0, 0, 0.096));
            }
        }
        
    }
    #message-window{
        z-index: 4;
        position: fixed;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background: rgba(0, 0, 0, 0.486);
        display: flex;
        align-items: center;
        justify-content: center;
        #message-container{
            position: relative;
            display: flex;
            flex-direction: column;
            justify-content: center;
            background: #fff;
            width: 16rem;
            border-radius: 4px;
            img{
                height: 9rem;
                width: 12rem;
                margin:  0 auto;
            }
            p{
                color: rgb(165, 165, 165);
                text-align: center;
                margin-top: .1em;
            }
            button{
                margin: 0 auto;
                background: #ff6762;
                border: none;
                font-size: 1.2em;
                padding: 0 1em;
                border-radius: .4em;
                text-shadow: 0px 1px 1px #f0221c;
                color: #fff;
                outline: none;
                margin-bottom: .2em;
                a{
                    text-decoration: none;
                    color: #fff;
                }
            }
            #close-btn{
                position: absolute;
                right: .5em;
                top: 0;
                font-size: 1.5em;
                color: #ccc;

            }
        }
    }
    #router-view{
        z-index: 1;
        position: relative;
        top: 5.6rem;
    }
}

</style>
